import * as type from "@/store/mutation-type";

const state = {
  oneMenuList: [], // 
  twoMenuList: [], // 
  btnList: {}, //  id key
  powerList: {}, // 
};

const mutations = {
  // 
  [type.ADD_ONE_MENU_LIST](state, payload) {
    state.oneMenuList = payload;
  },

  // 
  [type.ADD_BTN_LIST](state, payload) {
    state.btnList = payload;
  },

  // 
  [type.ADD_TWO_MENU_LIST](state, payload) {
    state.twoMenuList = payload;
  },

  //   
  [type.ADD_POWER_LIST](state, payload) {
    let bl = state.btnList;
    let tml = state.twoMenuList;

    tml.forEach((v) => {
      state.powerList[v.path] = bl[v.id];
    });
  },
};

export default {
  namespaced: true,
  state,
  mutations,
};
