export default {
  UserType(member) {
    switch (member) {
      case 0:
        return { name: "", color: "success" };
        break;
      case 1:
        return { name: "", color: "warning" };
        break;
      default:
        return { name: "", color: "info" };
        break;
    }
  },
  UserSex(sex) {
    switch (sex) {
      case 0:
        return { name: "", color: "warning" };
        break;
      case 1:
        return { name: "", color: "success" };
        break;
      default:
        return { name: "", color: "info" };
        break;
    }
  },

  MemberType(type) {
    switch (type) {
      case 1:
        return { name: "", color: "warning" };
        break;
      case 2:
        return { name: "", color: "success" };
        break;
      case 3:
        return { name: "", color: "danger" };
        break;
      case 4:
        return { name: "", color: "danger" };
        break;
      default:
        break;
    }
  },
};
