// 
// 
export const ruleName_en = (rule, value, callback) => {
  const reg = /^\w+$/;
  if (value === "") {
    return callback(new Error(""));
  }
  if (!reg.test(value)) {
    return callback(new Error("!"));
  }
  callback();
};

//  /^[\u4e00-\u9fa5\w]{3,9}$/g
export const ruleAccount = (rule, value, callback) => {
  const reg = /^[\u4e00-\u9fa5\w]{3,9}$/g;
  if (value === "") {
    return callback(new Error(""));
  }
  if (!reg.test(value)) {
    return callback(
      new Error("39")
    );
  }
  callback();
};

// path
export const ruleAddr = (rule, value, callback) => {
  const reg = /\/[\w\/]+/;
  if (value === "") {
    return callback(new Error(""));
  }
  if (!reg.test(value)) {
    return callback(new Error("! /xx "));
  }
  callback();
};

// 
export const ruleEmail = (rule, value, callback) => {
  const reg = /\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/;
  if (value === "") {
    return callback(new Error(""));
  }
  if (!reg.test(value)) {
    return callback(new Error("!"));
  }
  callback();
};

// 
export const ruleMobile = (rule, value, callback) => {
  const reg = /^1[3456789]\d{9}$/;
  if (value === "") {
    return callback(new Error(""));
  }
  if (!reg.test(value)) {
    return callback(new Error("!"));
  }
  callback();
};

export const rulePassword = (rule, value, callback) => {
  const reg = /^\w{6,18}$/;
  if (value === "") {
    return callback(new Error(""));
  }
  if (!reg.test(value)) {
    return callback(new Error("618"));
  }
  callback();
};
