import Vue from "vue";
import VueRouter from "vue-router";
import { Message } from "element-ui";

import routeList from "@/router/routeList.js";
import Home from "@/views/home/Home.vue";
import Login from "@/views/login/Login.vue";
import Welcome from "@/views/home/Welcome.vue";
import Messages from "@/views/message/Message.vue";
import Upgrade from "@/views/user/Upgrade.vue";

import store from "@/store/index.js";
import * as type from "@/store/mutation-type.js";

Vue.use(VueRouter);

// vuecli3catch
const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch((err) => err);
};

let routes = [
  {
    path: "/",
    redirect: "/home",
  },
  {
    path: "/login",
    component: Login,
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  // base: "/dist/",
  routes,
});

// 
router.isRouteAdd = false;
let stackCount = 0;

router.beforeEach((to, from, next) => {
  if (to.path == "/login") {
    window.sessionStorage.clear();
    console.log("");
    return next();
  }

  //2.  token
  const token = window.sessionStorage.getItem("token");

  //3. token
  if (!token) {
    Message.error("~");
    return next("/login");
  }

  console.log(" ");

  // 
  if (!router.isRouteAdd) {
    // if (store.state.loginSign != true) {
    //   console.log("");
    //   store.commit("toggleSign");
    // }

    let storage_menu = null;
    let storage_btn = null;
    let ownInfo = null;

    //   login
    try {
      storage_menu = JSON.parse(window.sessionStorage.getItem("menuList"));
      storage_btn = JSON.parse(window.sessionStorage.getItem("btnList"));
      ownInfo = JSON.parse(window.sessionStorage.getItem("ownInfo"));
    } catch (error) {
      console.log(error);
      Message.error("~");
      return next("/login");
    }
    // 
    if (storage_menu) {
      filterRoutes(storage_menu);
    }

    let menuList = [];

    // 
    extractData(storage_menu, menuList);
    addRoutes(menuList);

    //     vuex

    store.commit("menu/" + type.ADD_ONE_MENU_LIST, storage_menu);
    store.commit("menu/" + type.ADD_TWO_MENU_LIST, menuList);
    store.commit("menu/" + type.ADD_BTN_LIST, storage_btn);
    // 
    store.commit("menu/" + type.ADD_POWER_LIST);
    // 
    store.commit(type.EDIT_OWNINFO, ownInfo);

    // true 
    router.isRouteAdd = true;
  }

  // console.log("twoMenuList:", store.state.menu.twoMenuList);
  // console.log("btnList:", store.state.menu.btnList);

  console.log(to.matched);

  changeTabs(to.path);

  // matched 0
  if (to.matched.length === 0) {
    ++stackCount;
    if (stackCount > 4) {
      location.reload();
      return next("/login");
    }
    next({ ...to, replace: true });
    // next(to.path);
  } else {
    stackCount = 0;
    next();
  }
});
/**
 *
 *
 * 
 */
const changeTabs = (path) => {
  if (path == "/home") {
    return;
  }

  // 0. 
  let check = store.state.tabs.pageTabs.some((v) => {
    return v.name == path;
  });
  if (check) {
    store.commit("tabs/" + type.EDIT_ACTIVE_TAB, path); // 
    return console.log("");
  }
  let title = "";
  // 1.   
  let routes = router.options.routes;

  // tab
  try {
    routes.forEach((v) => {
      if (v.path == path) {
        title = v.name;
        throw title;
      }
      if (v.children) {
        v.children.forEach((v2) => {
          if (v2.path == path) {
            title = v2.name;
            throw title;
          }
        });
      }
    });
  } catch (title) {
    console.log("");
  }

  // 
  store.commit("tabs/" + type.INCRE_PAGE_TABS, {
    title,
    name: path,
  });
  store.commit("tabs/" + type.EDIT_ACTIVE_TAB, path);
};

/**
 *
 *
 * 
 */
// 
const filterRoutes = (res) => {
  for (let i = 0; i < res.length; i++) {
    if (res[i].component) {
      res[i].meta = res[i].meta != "" && JSON.parse(res[i].meta);
      res[i].path = res[i].addr;
      res[i].component = routeList[res[i].component];
    }
    // if (res[i].addr == "/agency") {
    //   res[i].meta = ["", ""];
    //   res[i].path = "/agency";
    //   res[i].component = routeList["Agency"];
    // }

    if (res[i].children) {
      filterRoutes(res[i].children);
    }
  }

  return res;
};

// 
const extractData = (data, list) => {
  //  children 
  data.forEach((v) => {
    if (v.children) {
      v.children.forEach((v2) => {
        list.push(v2);
      });
    }
  });
};

// 
const addRoutes = (list) => {
  let dynamicRoutes = {
    path: "/home",
    component: Home,
    redirect: "/home/welcome",
    children: [
      {
        path: "/home/welcome",
        component: Welcome,
        name: "",
        meta: [],
      },
      {
        path: "/message",
        component: Messages,
        name: "",
        meta: [],
      },
      {
        path: "/upgrade",
        component: Upgrade,
        name: "",
        meta: [],
      },
      ...list,
    ],
  };
  // 
  router.options.routes.push(dynamicRoutes);

  router.addRoutes([dynamicRoutes]);
};

export default router;
